.menu-ico {
  position: relative;
  width: 34px;
  height: 26px;
}

@media only screen and (min-width: 1022px) {
  .menu-ico {
    display: none;
  }
}

.menu-ico--active:before {
  transform: rotate(45deg) translate3d(1.5px, 1px, 0);
}

.menu-ico--active:after {
  transform: rotate(-45deg);
}

.menu-ico--active span {
  opacity: 0;
}

.menu-ico:before, .menu-ico:after {
  content: "";
  position: absolute;
  width: 100%;
  border-bottom: 1px solid #F7F9FB;
  transform-origin: 0 0;
  transition: transform 0.2s linear;
}

.menu-ico:after {
  bottom: 0;
}

.menu-ico span {
  position: absolute;
  top: 50%;
  width: 100%;
  margin-top: -0.5px;
  border-bottom: 1px solid #F7F9FB;
  transition: opacity 0.2s linear;
}

