    margin-bottom: 6px;
  }
}

.forecast__highlight:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 3px;
  border-radius: 3px;
  background: #a3a8af;
  transform: rotate(-65.5deg);
}

@media only screen and (min-width: 600px) {
  .forecast__highlight:before {
    position: absolute;
    top: -1px;
    right: 100%;
    width: 116px;
    height: 3px;
    margin-right: 10px;
    transform-origin: bottom right;
  }
}

.forecast__highlight span {
  vertical-align: middle;
  font-size: 21px;
  font-weight: 300;
}

@media only screen and (min-width: 600px) {
