.features {
	padding: 56px 0 12px;
	@include rmin($sm) {
		padding: 52px 0 56px; }
	@include rmin($sm-to-md) {
		padding: 72px 0 48px; }
	@include rmin($md-to-lg) {
		padding: 72px 0; }
	@include rmin($xl) {
		padding-bottom: 88px; }
	&__ttl {
		margin-bottom: 38px;
		text-align: center;
		font-size: 38px;
		font-weight: normal;
		line-height: 1.32;
		letter-spacing: -0.6px;
		color: #2e81ff;
		@include rmin($sm) {
			margin-bottom: 45px;
			font-size: 53px;
			font-weight: 100;
			line-height: 1.43;
			letter-spacing: -0.8px; }
		@include rmin($sm-to-md) {
			margin-bottom: 48px;
			font-size: 60px;
			line-height: 1.27;
			letter-spacing: -0.9px; }
		@include rmin($md-to-lg) {
			margin-bottom: 48px; } }
	&__btn {
		@include rmin($sm-to-md) {
			padding: 0 36px; }
		@include rmin($md) {
			padding: 0 75px; }
		@include rmin($md-to-lg) {
			padding: 0 80px; }
		@include rmin($lg) {
			max-width: 1314px;
			margin: 0 auto; } } }

.feature-icon {
	display: inline-block;
	width: 84px;
	height: 68px;
	background-size: contain;
	background-repeat: no-repeat;
	@include rmin($sm) {
		width: 108px;
		height: 87px; }
	$list: fast decentralized optimized;
	@each $item in $list {
		$index: index($list, $item);
		&_#{$item} {
			background-image: url(../img/multicolor-svg/feature/feature-#{$index}.svg); } } }


.case-icon {
	display: inline-block;
	width: 84px;
	height: 62px;
	background-size: contain;
	background-repeat: no-repeat;
	@include rmin($md-to-lg) {
		width: 94px;
		height: 86px; }
	$list: first second third forth fifth sixth;
	@each $item in $list {
		$index: index($list, $item);
		&_#{$item} {
			background-image: url(../img/multicolor-svg/cases/case-sm-#{$index}.svg);
			@include rmin($md-to-lg) {
				background-image: url(../img/multicolor-svg/cases/case-lg-#{$index}.svg); } } } }
