.metric {
	&__inner {
		margin: 0 -12px;
		padding: 59px 12px 12px;
		background-color: $charcoal-grey;
		@include rmin($sm) {
			margin: 0;
			padding: 59px 24px 24px; }
		@include rmin($sm-to-md) {
			padding: 84px 32px 32px; }
		@include rmin($md-to-lg) {
			padding: 84px 80px 80px; }
		@include rmin($lg) {
			padding: 92px 114px 80px; }
		@include rmin($lg-to-xl) {
			display: flex;
			flex-wrap: wrap;
			padding: 92px 28px 96px; } }
	&__table {
		margin-bottom: 52px;
		@include rmin($lg-to-xl) {
			padding: 0 28px;
			flex-basis: 50%; } } }

.metric-data {
	margin-top: 30px;
	@include rmin($lg) {
		margin-top: 45px; }
	&__el {
		margin-bottom: 15px;
		color: $pale-grey;
		@include rmin($sm) {
			margin-bottom: 22px; }
		@include rmin($lg) {
			margin-bottom: 28px; }
		&_orange {
			color: $dusty-orange;
			.metric-data__graph {
				background: $dusty-orange; } }
		&_pink {
			color: $hot-pink;
			.metric-data__graph {
				background: $hot-pink; } }
		&_blue {
			color: $dark-sky-blue;
			.metric-data__graph {
				background: $dark-sky-blue; } }
		&_green {
			color: $mid-green;
			.metric-data__graph {
				background: $mid-green; } } }
	&__graph {
		width: 100%;
		height: 6px;
		margin-bottom: 3px;
		border-radius: 3px;
		background: $silver;
		@include rmin($sm) {
			margin-bottom: 5px; } }
	&__text {
		font-size: 23px;
		font-weight: 300;
		line-height: 1.39;
		letter-spacing: -0.6px;
		@include rmin($sm) {
			font-size: 26px;
			font-weight: 500;
			line-height: 1.23;
			letter-spacing: -0.4px; } } }


.forecast {
	width: 100%;
	margin-top: 66px;
	@include rmin($md) {
		margin-top: 72px; }
	@include rmin($lg-to-xl) {
		margin-top: 28px; }
	&__wrap {
		margin-bottom: 8px;
		@include rmin($sm) {
			margin-bottom: 74px; }
		@include rmin($sm-to-md) {
			margin-bottom: 80px; }
		@include rmin($lg-to-xl) {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 20px; } }
	&__title {
		margin-bottom: 40px;
		text-align: center;
		font-size: 37px;
		line-height: 1.35;
		letter-spacing: -0.5px;
		color: rgba($pale-grey, 0.88);
		@include rmin($sm) {
			font-size: 54px;
			font-weight: 100;
			letter-spacing: -0.8px; }
		@include rmin($sm-to-md) {
			margin-bottom: 58px;
			font-size: 62px;
			font-weight: 300;
			line-height: 1.23;
			letter-spacing: -0.9px; } }
	&__bl {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 40px;
		text-align: center;
		font-size: 17px;
		color: rgba($pale-grey, 0.88);
		line-height: 1.41;
		letter-spacing: -0.3px;
		@include rmin($sm) {
			text-align: left; }
		@include rmin($sm-to-md) {
			margin-bottom: 44px; }
		@include rmin($md) {
			margin-bottom: 68px; }
		@include rmin($lg) {
			margin-bottom: 92px; }
		@include rmin($lg-to-xl) {
			flex-basis: 50%;
			margin-bottom: 68px;
			padding: 0 28px; } }
	&__el {
		flex-basis: 100%;
		margin-bottom: 12px;
		@include rmin($sm) {
			flex-basis: 33%;
			line-height: 1.33; } }
	&__statement {
		font-size: 21px;
		font-weight: 500;
		color: $pale-grey;
		@include rmin($sm-to-md) {
			padding-right: 20px;
			font-size: 26px;
			font-weight: 500;
			line-height: 1.35;
			letter-spacing: -0.4px; }
		@include rmin($md) {
			max-width: 274px; }
		@include rmin($lg) {
			padding-right: 0; } }
	&__info {
		flex-basis: 100%;
		@include rmin($sm) {
			flex-basis: 66%;
			padding-left: 30px; }
		@include rmin($lg-to-xl) {
			padding-left: 50px; } }
	&__highlight {
		position: relative;
		margin-bottom: 12px;
		font-size: 0;
		color: #f5a623;
		@include rmin($sm) {
			margin-bottom: 4px; }
		@include rmin($sm-to-md) {
			margin-bottom: 6px; }
		&:before {
			content: "";
			display: inline-block;
			vertical-align: middle;
			width: 24px;
			height: 3px;
			border-radius: 3px;
			background: $cool-grey;
			@include rotate(-65.5);
			@include rmin($sm) {
				position: absolute;
				top: -1px;
				right: 100%;
				width: 116px;
				height: 3px;
				margin-right: 10px;
				transform-origin: bottom right; } }
		span {
			vertical-align: middle;
			font-size: 21px;
			font-weight: 300;
			@include rmin($sm) {
				font-size: 24px;
				font-weight: 300;
				line-height: 1.58; }
			@include rmin($sm-to-md) {
				font-size: 29px;
				line-height: 1.31;
				letter-spacing: -0.4px; } } }
	&__button {
		@include rmin($lg-to-xl) {
			padding: 0 75px; }
		@include rmin($xl) {
			padding: 0 91px; } } }
